import { BASE_IMAGE_URL,getKucun,setKucunYujing,addSaleOrder } from '@/utils/api'
import uniPopup from '@/components/uni-popup.vue'
import uniLoadMore from '@/components/uni-load-more.vue';
export default {
	components: {
		uniLoadMore,
		uniPopup
	},
	data() {
		return {
			loadingText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			list: [],
			loadingType: 0,
			page: 1,
			typeList: ['综合', '最新', '价格', '库存'],
			curType: 0,
			g_type: 0,
			type:'',
			companyCode: '',
			o_name: '',
			o_phone: '',
			o_num: '',
			sellType: '',
			m_role: 0,
			reportForm: true
		}
	},
	methods: {
		formSubmit: function(e) {
			console.log('form发生了submit事件，携带数据为：', e.detail.formId);
			this.addSaleOrder(e.detail.formId);
		 },
		hidePop(){
			this.type="";
		},
		hideSellPop(){
			this.sellType = "";
		},
		setSellValue(index){
			uni.setStorageSync("good_list", this.list[index]);
			uni.navigateTo({
				url:'../sell_goods/sell_goods'
			});
			return;
			this.sellType = "middle";
			this.o_name = "";
			this.o_phone = "";
			this.o_num = "";
			this.curType = index;
		},
		async addSaleOrder(formid){
			
			if(!this.o_name){
				uni.showToast({
					icon: 'none',
					duration: 1000,
					title: "请输入姓名"
				});
				return;
			};
			
			if(!this.o_phone){
				uni.showToast({
					icon: 'none',
					duration: 1000,
					title: "请输入手机号"
				});
				return;
			};
			
			if(!this.o_num || (this.o_num == 0)){
				uni.showToast({
					icon: 'none',
					duration: 1000,
					title: "请输入正确的数值"
				});
				return;
			};
			
			let params = {
				o_name: this.o_name,
				o_phone: this.o_phone,
				go_g_id: this.list[this.curType].g_id,
				go_count: this.o_num,
				go_g_price: this.list[this.curType].g_price,
				go_g_name: this.list[this.curType].g_name,
				go_g_img: this.list[this.curType].g_img,
				o_openid: uni.getStorageSync("openid"),
				go_t_is_no: this.list[this.curType].go_t_is_no,
				formid: formid
			};
			let res = await addSaleOrder(params);
			if(res.code == 1000){
				this.sellType = "";
				this.getList();
			}else{
				uni.showToast({
					icon: 'none',
					duration: 1000,
					title: res.msg
				});
			}
		},
		async bindCompanyCode(){
			if(!this.companyCode || (this.companyCode == 0)){
				uni.showToast({
					icon: 'none',
					duration: 1000,
					title: "请输入正确的数值"
				});
				return;
			};
			let params = {
				k_g_id: this.list[this.curType].g_id,
				k_m_id: uni.getStorageSync("userInfo").m_id,
				k_yj_value: this.companyCode
			};
			
			let res = await setKucunYujing(params);
			if(res.code == 1000){
				uni.showToast({
				  icon: 'none',
				  title: '设置成功',
				  duration: 1000
				});
				this.getList();
			}
			this.type="";
			
		},
		setYuJing(index){
			console.log(index);
			this.curType = index;
			this.type = 'middle';
		},
		async getList(){
			let res = await getKucun(this.page);
			uni.stopPullDownRefresh();
			if(res.code == 1000){
				if(this.page == 1){
					this.list = res.data;
				}else{
					this.list = this.list.concat(res.data);
				}
				
				if(res.data.length < 10){
					this.loadingType = 2;
				}else{
					this.loadingType = 0;
				}
			}
		},
		goToDetail(index){
			uni.navigateTo({
				url: '/pages/goods_detail/goods_detail?g_id='+this.list[index].g_id
			});
		},
		async jiaruCart(item){
			let params = {
				ct_openid: uni.getStorageSync("openid"),
				ct_g_id: item.g_id,
			};
			let res = await jiaruCart(params);
			if(res.code == 1000){
				uni.showToast({
				  icon: 'none',
				  title: '添加成功',
				  duration: 1000
				});
			}else{
				uni.showToast({
				  icon: 'none',
				  title: '加入购物车失败',
				  duration: 1000
				});
			}
		},
	},
	onReachBottom() {
		this.loadingType = 1;
		this.page++;
		this.getList();
	},
	
	onPullDownRefresh() {
		console.log("dddddd");
		this.page = 1;
		this.list = [];
		this.getList();
	},
	onLoad(options){
		this.g_type = options.g_type;
		this.m_role = uni.getStorageSync("userInfo").m_role;
		
	},
	onShow(){
		this.getList();
	}
	
}
